/* General Styles */
body {
    font-family: Arial, sans-serif;
    background-color: #F4E3C7;
    /* Cream Beige */
    color: #4B3832;
    /* Warm Espresso */
    margin: 0;
    /* Remove default body margin for better full-width images */
}

/* Header Styling */
header {
    display: flex;
    align-items: center;
    /* Vertically center content */
    justify-content: space-between;
    /* Space between logo and nav */
    background-color: #8A6E44;
    /* Dark Gold */
    color: #F4E3C7;
    /* Cream Beige */
    padding: 20px;
    width: 100%;
    box-sizing: border-box;
}

/* Navigation Menu */
nav {
    margin-left: auto;
    /* Push nav to far right side */
}

nav ul {
    list-style: none;
    display: flex;
    justify-content: center;
    /* Horizontally center items in nav */
    align-items: center;
    padding: 0;
    margin: 0;
}

nav ul li {
    margin: 0 1.5em;
}

nav ul li a {
    color: #F4E3C7;
    /* Cream Beige */
    text-decoration: none;
    font-size: 1.125rem;
    /* Using rem for scalability */
    font-weight: bold;
}

nav ul li a:hover {
    color: #A58F7F;
    /* Soft Taupe */
}

/* Section Styling */
section {
    padding: 2em;
}

/* Footer Styling */
footer {
    background-color: #8A6E44;
    /* Dark Gold */
    color: #F4E3C7;
    /* Cream Beige */
    text-align: center;
    padding: 1em;
}

/* Logo Styling (shared between header and footer) */
.header-logo {
    width: 150px;
    max-width: 100%;
    height: auto;
    /* Responsive logo */
    margin-right: 20px;
    /* Optional: Add some spacing from nav */
}

.footer-logo {
    width: 100px;
    display: block;
    margin: 0 auto;
    padding: 10px 0;
    height: auto;
    /* Responsive logo */
}

/* Textarea Styling */
textarea {
    width: 100%;
    height: 30vh;
    resize: none;
    box-sizing: border-box;
    padding: 10px;
    background-color: #D4B483;
    /* Nude Gold */
    color: #4B3832;
    /* Warm Espresso */
    border: 1px solid #A58F7F;
    /* Soft Taupe */
}

/* CTA Buttons Styling */
.cta-buttons {
    display: flex;
    justify-content: center;
    /* Horizontally center buttons */
    align-items: center;
    /* Vertically center buttons */
    gap: 20px;
    /* Adds space between the buttons */
    margin-top: 20px;
    /* Optional: Add space above the buttons */
}

.cta-buttons button {
    padding: 10px 20px;
    /* Optional: Add padding to the buttons */
    font-size: 16px;
    /* Optional: Adjust the font size */
    background-color: #8A6E44;
    /* Dark Gold */
    color: #F4E3C7;
    /* Cream Beige */
    border: none;
    cursor: pointer;
    transition: background-color 0.3s;
}

.cta-buttons button:hover {
    background-color: #A58F7F;
    /* Soft Taupe on hover */
}

/* Consistent Spacing for Input Elements */
textarea,
input[type="text"],
input[type="email"] {
    padding: 10px;
    border: 1px solid #A58F7F;
    /* Soft Taupe */
    box-sizing: border-box;
    width: 100%;
    margin-bottom: 15px;
}

/* Form Submit Button Styling */
form button[type="submit"] {
    background-color: #8A6E44;
    /* Dark Gold */
    color: #F4E3C7;
    /* Cream Beige */
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s;
}

form button[type="submit"]:hover {
    background-color: #A58F7F;
    /* Soft Taupe */
}

/* Section Image Container Styling */
.section-image-container {
    position: relative;
    width: 100%;
    height: 50vh;
    /* Container is always half the size of the image */
    overflow: hidden;
    margin: 40px 0;
    /* Add margin to space out images from content */
}

/* Section Image Styling for Scroll within Container */
.section-image {
    width: 100%;
    height: 200%;
    /* Image is twice the height of the container */
    object-fit: cover;
    /* Maintain aspect ratio while covering container */
    transform: translateY(0);
    /* Initial position */
    transition: transform 0.1s linear;
    /* Smooth scrolling */
}